<template>
    <div class=" p-2 rounded-2 border border-primary border-2">
        <h4 class="">{{mensaje}}</h4>
        <div class="mt-3 float-end">
            <button type="button" class="btn btn-secondary m-1" :disabled="loading ?? false"
                data-bs-dismiss="modal">
                <i class="bx bx-exit"></i> Salir</button>
            <button type="button" class="btn btn-primary m-1" data-bs-dismiss="modal"
                @click="()=>{$emit('continuar', json.key);}" :disabled="loading ?? false">
                <i class="bx" :class="{'bx-check':!loading, 'bx-spin bx-loader':loading}"></i> Continuar
            </button>
        </div>
    </div>
</template>
<script>
import { defineComponent, ref } from 'vue'
export default defineComponent({
    props:[
        'mensaje',
        'loading'
    ],
    setup:(props, {emit})=>{
        const json = ref(null)
        const setJson = (_json) =>{
            console.log(_json.key)
            json.value = _json;
        }
        
        return {
            json,
            setJson
        }
    }
    
})
</script>